@font-face {
  font-family: "Jost";
  src: url("../src/assets/reg.woff") format("woff2"),
    url("../src/assets/reg.woff") format("woff");
}
/*
* CSS VARIABLES
*/
:root {
  --root-background: #ffe05d;
  --text-color: rgb(172, 75, 28);
  --btn-primary: #ff9642;
  --heading-color: #f16821;

  --brown: #ac4b1c;
  --orange: #ff9642;
  --light-text: #ffefa0;
  --green-yellow: greenyellow;
  --navy: navy;
  --black: black;
  --purple: #6a097d;
}

.theme-dark {
  --root-background: black;
}

body {
  margin: 0;
  font-family: "Jost", Fallback, sans-serif;
  -webkit-font-smoothing: antialia<link rel= "stylesheet" href=
    "https://cdnjs.cloudflare.com/ajax/libs/font-awesome/4.7.0/css/font-awesome.min.css"
    >sed;
  -moz-osx-font-smoothing: grayscale;
  background-color: var(--root-background);
  color: #f16821;
  /* background-size: 100% 80%; */
}

.holder {
  background-color: grey;
  height: 20px;
}

button:focus {
  outline: 0;
}

button:hover {
  cursor: pointer;
}

.hide {
  display: none !important;
}

.position--fixed {
  position: fixed;
}

.error {
  color: red;
}
.error-border {
  border: solid 1px red;
}
